.status-circle{
    cursor: pointer;
    height: 25px;
    width: 25px;
    color:#fff;
    border-radius: 50px;
    font-size: 12px;
    text-align: center;
    align-self: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.date-css{
    align-self: center;
}

.main-header{
    border: 1px solid #c5c5c5;
    border-radius: 10px;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
}

.header-detail{
    align-items: center;
}

.header-task{
    display: flex;
    justify-content: space-evenly;
    /* border-right: 1px solid #c5c5c5;
    height: 100%; */
}

.header-date{
    display: flex;
    justify-content: flex-end;
    /* border-right: 1px solid #c5c5c5;
    height: 100%; */
}

.header-progress{
    /* border-right: 1px solid #c5c5c5;
    height: 100%;
    padding-top: 13.5px; */
    display: flex;
    justify-content: space-around;
}

.header-members{
   
}

.text-area-task{
    resize: none;
}

.simplebar-wrapper{
    /* height: 400px; */
    overflow: hidden;
}
.css-qw2e6q{
    height: 400px;
}
.simplebar-content-wrapper{
    overflow: scroll;
}

.task-label{
    font-family: "Montserrat", sans-serif;
}

.comments-outerContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    /* margin-bottom: 5px; */
  }
  
  .comments-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #FFFFFF;
    border-radius: 8px;
    height: 48vh;
    width: 92%;
  }

  .comments-field{
      background-color: rgb(0 42 21 / 8%) ;
      font-size: 12px;
      resize: none;
  }
  .comments-input{
      display: flex;
  }

  .messages {
    flex: auto;
    border: 1px solid #c5c5c5;
    overflow: auto;
  }

  .messageBox {
    background: #F3F3F3;
    border-radius: 20px;
    padding: 5px 20px;
    color: white;
    display: inline-block;
    max-width: 80%;
  }

  .messageBox {
    background: #F3F3F3;
    border-radius: 20px;
    padding: 5px 10px;
    color: white;
    display: inline-block;
    max-width: 80%;
  }

  .messageText {
    width: 100%;
    letter-spacing: 0;
    float: left;
    font-size: 1.1em;
    word-wrap: break-word;
  }
  
  .messageText img {
    vertical-align: middle;
  }
  
  .messageContainer {
    display: flex;
    justify-content: flex-end;
    padding: 0 5%;
    margin-top: 3px;
  }
  
  .sentText {
    display: flex;
    align-items: center;
    font-family: Helvetica;
    color: #828282;
    letter-spacing: 0.3px;
  }

  .comments-user-image{
      width: 30px;
      height: 30px;
      border-radius: 50%;
  }
  
  .pl-10 {
    padding-left: 10px;
  }
  
  .pr-10 {
    padding-right: 10px;
  }
  
  .justifyStart {
    justify-content: flex-start;
  }
  
  .justifyEnd {
    justify-content: flex-end;
  }
  
  .colorWhite {
    color: white;
  }
  
  .colorDark {
    color: #353535;
  }
  
  .backgroundBlue {
    background: #00AB55;
  }
  
  .backgroundLight {
    background: #F3F3F3;
  }

  .infoBar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #00AB55;
    border-radius: 4px 4px 0 0;
    height: 20px;
    width: 100%;
  }
  
  .leftInnerContainer {
    flex: 0.5;
    display: flex;
    align-items: center;
    margin-left: 2%;
    color: white;
  }
  
  .rightInnerContainer {
    display: flex;
    flex: 0.5;
    justify-content: flex-end;
    margin-right: 2%;
  }
  
  .onlineIcon {
    margin-right: 5%;
  }
